.bag-box {
  margin: 20px 140px 40px 140px;
}

.bag-title {
  font-size: var(--extra-large-font-size);
  padding-top: 0px;
  padding-bottom: 28px;
  text-align: center;
  letter-spacing: 0;
}

.bag-container {
  display: flex;
  justify-content: space-between;
  position: relative;
  gap: 32px;
}

.bag-left-container {
  width: 62%;
}

.bag-left-container-title {
  padding-bottom: 16px;
  font-size: var(--middle-font-size);
  letter-spacing: 0;
}

.bag-left-container-title-with-padding {
  padding-right: 10px;
}

.delivery-span {
  padding-bottom: 16px;
  font-size: var(--main-font-size);
}

.bag-left-sub-container {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 8px 32px -12px,
    rgba(0, 0, 0, 0.3) 0px 6px 12px -18px;
  padding: 32px;
  padding-bottom: 0px;
}

.bag-left-sub-left-container {
  display: flex;
  padding-bottom: 32px;
}

.delivery-icon {
  color: var(--dark-pink);
}

.bag-right-container {
  width: 32%;
}

.bag-right-container-sticky {
  position: sticky;
  top: calc(18vh + 42px);
}

.bag-right-container-title {
  font-size: var(--middle-font-size);
  padding-bottom: 16px;
}

.bag-right-sub-container-bold {
  font-family: var(--bold-italic-font-family);
  letter-spacing: 0;
}

.bag-right-sub-container {
  position: relative;
  letter-spacing: 0;
}

.bag-right-sub-right-container {
  display: flex;
}

.bag-right-sub-container-absolute {
  position: absolute;
  right: 0;
}

.bag-right-sub-container p {
  padding: 0;
  margin: 0;
  padding-bottom: 8px;
}

.continue-checkout-button {
  width: 100% !important;
  margin-top: 12px;
}

.flex-container-line {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px 0px;
}

.hr-line {
  width: 100%;
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: 0;
  box-shadow: 0 0 0 0.2px var(--dark-pink);
  border-top: 0px;
  border-bottom: 0px;
}

.line-img {
  width: 20px;
  vertical-align: middle;
  margin: 0px 4px;
}

.continue-checkout-button {
  padding: 12px;
  font-size: var(--medium-font-size);
  font-family: var(--bold-italic-font-family);
  border: none;
  cursor: pointer;
  overflow: hidden;
  background: var(--button-pink);
  color: var(--white);
  outline: none;
  letter-spacing: 1.2px;
  width: 100%;
  text-align: center;
}

.continue-checkout-button:hover {
  background: var(--button-hover-pink);
}
